<template>
  <v-container>
    <BarChart :chartData="chartData" :options="options" v-if="loaded" />
  </v-container>
</template>

<script>
// @ is an alias to /src
import BarChart from "@/components/BarChart.vue";
import axios from "axios"
export default {
  components: {
    BarChart
  },
  data () {
    return {
      chartData: [],
      options: [],
      key: [],
      value: [],
      target: this.$route.params.target,
      loaded: false
    }
  },
  created() {
    this.getDistribution(this.target)
  },
  methods: {
    getDistribution(target) {
      axios.get(`${this.$batchAPIUri}/job/distribution/${target}`)
        .then(res => { 
          this.loaded = true
          let navigate = this.$router
                    
          for(let i=0; i<res.data.length; i++) {
            this.key[i] = res.data[i].key;
            this.value[i] = res.data[i].value;
          }
          // chartData
          this.chartData = {
            labels: this.key,
            datasets: [
              {
                label: 'Batch execution count per minute(Last 30 days total)',
                backgroundColor: '#f87979',
                pointBackgroundColor: 'white',
                borderWidth: 1,
                pointBorderColor: '#249EBF',
                //Data to be represented on y-axis
                data: this.value
              }
            ]
          },
          // options
          this.options = {
            onClick: function(point, event) {
              if(event.length <=0) return
              let value = event[0]['_model']['label']
              // 여기서 this.$router.push() 하게되면 this는 onClick 함수를 의미함. 함수 밖에서 선언해야함.
              navigate.push({ name: 'contribution', params: {target: value}})
            },
            scales: {
              yAxes: [{
                ticks: {
                  beginAtZero: true
                },
                gridLines: {
                  display: true
                }
              }],
              xAxes: [ {
                gridLines: {
                  display: false
                }
              }]
            },
            legend: {
                display: true
            },
            responsive: true,
            maintainAspectRatio: false
          }      
        })
        .catch(err => {
          console.error(err)
        })
    },
  }
};
</script>